@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


@font-face {
  font-family: 'MyWebFont';
  src: url(/cv/static/media/NixieOne.c067ab7e.ttf) format('truetype');
}

/*
main styles
*/

body{
  margin:0;
  padding:0;
  box-sizing: border-box;
  background: url(/cv/static/media/background.c75d2a39.png);
  background-repeat: no-repeat;
  width:100%;
  height: 100vh;
  background-size: 100%;
  background-position: 50%;
  font-family: "Montserrat"

}


.App{
  position: relative;
  width: 100%;
  height: 100vh;
}



.container{
  height: 100vh;
}

.rutaUnder{
  width: 100%;
  height: 0;
  background: rgba(0,0,0,0.9);
  position: absolute;
  bottom:0;
}

.rutaUnder-enter-active{
  height: 30px;
  transition:height 200ms ease-out;
}

.rutaUnder-enter-done{
  height: 30px;
  position: absolute;

  bottom:0;
}

.rutaUnder-exit{
  height: 30px;
  position: absolute;
  bottom:0;
}

.rutaUnder-exit-active{
  height: 0px;
  position: absolute;
  bottom:0;
  transition:height 200ms ease-out;
}

.rutaUnder-exit-done{
  height: 0px;
  position: absolute;
  bottom:0;
}

.ruta{
  width: 100%;
  height: 0px;
  background: rgba(0,0,0,0.9);
  position: absolute;
  background:"red"
}


.ruta-enter-active {
  height: 20px;
  transition:height 200ms ease-out;
}

.ruta-enter-done{
  height: 20px;

}

.ruta-exit{
  height: 20px;

}

.ruta-exit-active{
  height: 0px;
  transition:height 200ms ease-out;

}

.ruta-exit-done{
  height: 0px;

}

.startBtn{
  position: relative;
  top:50vh;
}

.cartbtn {
  text-align: center;
  position:relative;
  display:block;
  font-size: 1.7em;
  padding:10px;
  opacity: 0;
  top:-100px;
  width:300px;
  margin: auto;
  color:rgba(0,0,0,0.7);
  font-family: 'Montserrat', sans-serif;
  cursor: pointer;
}

.fade-appear-active {
  top:0px;
  opacity: 1;
  transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
}

.fade-enter-done{

  opacity: 1;
  top:0px;

  transition: color, -webkit-transform 0.4s;

  transition: transform 0.4s, color;

  transition: transform 0.4s, color, -webkit-transform 0.4s;
}


.cartbtn:hover{
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  color:rgba(255,255,255,0.7)
}

.fade-exit{
  position:relative;
  display: block;
  font-size: 1.2em;
  opacity: 1;
  top:0px;
  margin: auto;
  font-family: 'Montserrat', sans-serif;
  border:none;
  cursor:pointer;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
}

.fade-exit-done{
  opacity: 0;
}


#viewCart:hover{
  -webkit-transform: scale(0.9);
          transform: scale(0.9)
}

#viewCart:focus{
  outline: none;
}


.line-enter{
  margin: auto;
  width:0px;
  height: 2px;
  background: rgba(255,255,255,0.7);
}

.line-enter.line-enter-active{
  margin: auto;
  width:140px;
  height: 2px;
  background: rgba(255,255,255,0.7);
  transition: width 400ms ease-out ;

}

.line-enter-done{
  margin: auto;
  width:140px;
  height: 2px;
  background: rgba(255,255,255,0.7);
}



@font-face {
  font-family: "MyWebFont";
  src: url(/cv/static/media/NixieOne.c067ab7e.ttf) format("truetype");
}

.font {
  font-family: "Montserrat";
  font-weight: 1000;
  font-size: 120px;
  padding-bottom: 100px;
  color: rgba(0, 0, 0, 0.8);
}



.profile {
  width: 200px;
  border-radius: 50%;
  margin-left: 200px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
}


.wrapper {
  opacity: 1;
  text-align: center;
  width: 100%;
  margin: auto;
  background: rgb(238, 238, 238);
  position: absolute;
  bottom: 0px;
}

/* Profile Image */
/*****************/

.small {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

/*Small image */

.smallImageContainer {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 200;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

/* NAME */
/********/

.underline {
  height: 10px;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: -10px;
}



/* SKILLS */



.skillHead {
  font-family: "Montserrat";
  font-weight: 1000;
  font-size: 40px;
  color: rgba(255, 255, 255, 0.8);
}


/* CONTENT */

.content {
  height: 1400px;
  background: rgb(241, 241, 241);
  position: relative;
}

/* SKILLS IMAGES */

.skillsContent {
  position: relative;
  width: 45%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.iconContainer {
  transition-property: -webkit-transform, -webkit-filter;
  transition-property: transform, filter;
  transition-property: transform, filter, -webkit-transform, -webkit-filter;
  transition-duration: 0.2s, 0.6s;
}

.skillImage {
  width: 80px;
}

/*Projects */

.projectHeader {
  display: block;
  margin-bottom: 20px;
  font-family: "Montserrat";
  font-weight: 1000;
  text-align: center;
  font-size: 40px;
  color: rgba(0, 0, 0, 0.8);
}

.project {
  border-radius: 5px;
  display: "flex";
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.projectTextInfo {
  font-size: 12px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: left;
}

.projectName{
  transition: all 0.4s;
  background-color: rgba(0,0,0,0.8);
  color: rgba(255,255,255,0.8);
  margin:0;
  padding:5px;
  border-radius:5px 5px 0 0;
}



/* CV Transition*/
/************/

.showCv-enter {
  height: 0;
}

.showCv-active {
  opacity: 1;
  height: 80%;
  transition: height 500ms ease-in-out, opacity 500ms;
}

.showCv-enter-done {
  opacity: 1;
  height: 80%;
  transition: height 500ms ease-in-out, opacity 500ms;
}

.showCv-exit {
  opacity: 1;
  height: 150%;
}
.showCv-exit-active {
  height: 0;

  transition: height 500ms ease-in;
}

.showCv-exit-done {
  height: 0;
  display: none;
}

